* {
	box-sizing: border-box;
}

body {
	text-align: center;
	background-color: #333;
	color: white;
}

#drum-machine {
  width: clamp(300px, 90vw, 1280px);
	display: flex;
  flex-flow: row wrap-reverse;
  justify-content: space-between;
	gap: 2rem;
  border: 2px solid #333;
  border-radius: 0.5rem;
  margin: 0 auto;
  padding: 2rem;
	background-color: white;
	color: #333;
}

.container > button.drum-pad {
  font-size: 3rem;
}

.grid {
	display: grid;
  grid-template-columns: repeat(3, minmax(100px, 10vw));
  grid-template-rows: repeat(3, minmax(100px, 10vw));
  grid-gap: 0.25rem;
  align-items: stretch;
  justify-content: center;
}

.flex {
	display: flex;
  flex-direction: column;
	font-size: 2rem;
  justify-content: space-evenly;
	gap: 2rem;
}

.control { 
	background-color: #333;
	color: white;
	padding: 1rem;
	border-radius: 0.5rem;
	display: flex;
	flex-flow: row nowrap;
	gap: 1rem;
}

aside {
	font-size: 1rem;
	font-style: italic;
}

/* switches CSS (from w3 schools) */

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
} 
